<template>
    <div>
        <swiper :options="swiperOption">
            <swiper-slide @click.native="clickHandle(item.id)" v-for="item in dataList" :key="item.id" class="swiper-item">
                <img :src="item.cover.url" alt="">
                <p>{{item.title}}</p>
            </swiper-slide>
            <div class="swiper-pagination" slot="pagination"></div>
            <div class="swiper-button-prev" slot="button-prev"></div>
            <div class="swiper-button-next" slot="button-next"></div>
        </swiper>
    </div>
</template>
<script>
    import 'swiper/dist/css/swiper.css';
    import { swiper, swiperSlide } from 'vue-awesome-swiper';
    export default {
        components: {
            swiper,
            swiperSlide
        },
        data() {
            return {
                dataList: [],
                swiperOption: {
                    slidesPerView: 3,
                    spaceBetween: 30,
                    slidesPerGroup: 3,
                    loop: true,
                    loopFillGroupWithBlank: true,
                    pagination: {
                        el: ".swiper-pagination",
                        clickable: true
                    },
                    navigation: {
                        nextEl: ".swiper-button-next",
                        prevEl: ".swiper-button-prev"
                    }
                }
            }
        },
        created() {
            this.getData();
            let obj = {
                title: "书籍",
                navName: "book"
            }
            this.$emit("changeActive", obj)
        },
        methods: {
            getData() {
                let birdapi = 'https://bird.ioliu.cn/v2?url=';
                let thisUrl = "https://m.douban.com/rexxar/api/v2/subject_collection/book_fiction/items?start=0&count=20";
                axios.get(birdapi + thisUrl).then((res) => {
                    this.dataList = res.data.subject_collection_items;
                    // console.log(this.dataList);
                });
            },
            clickHandle(id){
                this.$router.push({
                    name:"bookdetail",
                    params:{
                        id
                    }
                })
            }
        },
    }
</script>

<style scoped>
    .swiper-item {
        width: 2rem;
        height: 5rem;
    }
    .swiper-item img{
        width: 100%;
    }
    .swiper-item p{
        font-size: .4rem;
    }
</style>